import './Admin.scss';
import { auth } from './resources/Firebase'
import React, { Suspense } from 'react';
import { Route, Switch, NavLink, Redirect } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLink, faPencilAlt, faEye, faUndoAlt, faSignOutAlt } from '@fortawesome/free-solid-svg-icons'

const CreateLinks = React.lazy(() => import('./views/CreateLinks'));
const EditDesign = React.lazy(() => import('./views/EditDesign'));
const WebVisits = React.lazy(() => import('./views/WebVisits'));

const Admin = () => {
    function Logout() {
        auth.signOut().then(() => {
            window.location.href='/'
        }).catch((error) => {
            console.log(error)
        });
    }
    return (
        <>
            <Suspense fallback={<div></div>}>
                <div className="row">
                    <div className="col">
                        <ul className="nav nav-pills mt-3" id="pills-tab" role="tablist">
                            <li className="nav-item pill-1 mr-2">
                                <NavLink className="nav-link" to="/admin/create-links"><FontAwesomeIcon icon={faLink} /></NavLink>
                            </li>
                            <li className="nav-item pill-1 mr-2">
                                <NavLink className="nav-link" to="/admin/edit-design"><FontAwesomeIcon icon={faPencilAlt} /></NavLink>
                            </li>
                            <li className="nav-item pill-1 mr-2">
                                <NavLink className="nav-link" to="/admin/web-visits"><FontAwesomeIcon icon={faEye} /></NavLink>
                            </li>
                            <li className="nav-item pill-1 mr-2">
                                <NavLink className="nav-link" activeClassName="none" to="/"><FontAwesomeIcon icon={faUndoAlt} /></NavLink>
                            </li>
                            <li className="nav-item pill-2 mr-2">
                                <NavLink className="nav-link" activeClassName="none" to="/" onClick={Logout}><FontAwesomeIcon icon={faSignOutAlt} /></NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
                <hr />
                <Switch>
                    <Route path="/admin/create-links" component={() => <CreateLinks />}/>
                    <Route path="/admin/edit-design" component={() => <EditDesign />} />
                    <Route path="/admin/web-visits" component={() => <WebVisits />} />
                    <Redirect to="/admin/create-links" />
                </Switch>
            </Suspense>
        </>
    )
}

export default Admin;


